import {Component, ElementRef, HostListener, OnInit, ViewChild} from '@angular/core';

import * as c3 from 'c3';
import {AbstractPageComponent, CustomerProfile, DialogService} from '../../../ic-core';
import {AccountService} from '../../services/account.service';
import {ActivatedRoute, ActivatedRouteSnapshot} from '@angular/router';
import {Customer} from '../../../ic-core';
import {CustomerInformationComponent} from '../../components/customer-information/customer-information.component';
import {CustomerProfileComponent} from '../../components/customer-profile/customer-profile.component';


const title = 'Customer Detail';
const breadcrumb = [{title: 'Account'}, {link: '/accounts/customers', title: 'Customers'}, {title: 'Customer Detail'}];

@Component({
  selector: 'ic-customer-detail',
  templateUrl: './customer-detail.component.html',
  styleUrls: ['./customer-detail.component.scss']
})
export class CustomerDetailComponent extends AbstractPageComponent implements OnInit {

  services: Array<string> = ['Botox', 'Filler', 'Da'];

  customerProfile: CustomerProfile;
  customer: Customer;

  @ViewChild('informationComponent', {static: false})
  public informationComponent: CustomerInformationComponent;

  @ViewChild('profileComponent', {static: false})
  public profileComponent: CustomerProfileComponent;

  constructor(private accountService: AccountService, private route: ActivatedRoute,
              private dialogService: DialogService) {
    super(title, breadcrumb);
  }

  async ngOnInit() {
    const customerId = this.route.snapshot.paramMap.get('customerId');
    this.customer = await this.accountService.getCustomer(customerId);
    this.customerProfile = await this.accountService.getCustomerProfile(customerId);
  }

  async canDeactivate(): Promise<boolean> {
    if ((this.informationComponent && this.informationComponent.hasChanged())
      || (this.profileComponent && this.profileComponent.hasChanged())) {
      return await this.dialogService.openConfirm('Leave site?' , 'Changes you made may not be saved');
    } else {
      return true;
    }
  }

  @HostListener('window:beforeunload', ['$event'])
  onBeforeUnload($event) {
    if ((this.informationComponent && this.informationComponent.hasChanged())
      || (this.profileComponent && this.profileComponent.hasChanged())) {
      return $event.returnValue = 'Changes you made may not be saved';
    }
  }
}
