import {Component, OnInit, ViewChild} from '@angular/core';
import {AccountService} from '../../services/account.service';
import {ActivatedRoute} from '@angular/router';
import {AbstractPageComponent} from '../../../ic-core/pages/abstract-page/abstract-page.component';
import {Address, Contact, Customer, CustomerProfile, DialogService, Phone} from '../../../ic-core';
import {CustomerInformationComponent} from '../../components/customer-information/customer-information.component';


const title = 'Create Customer';
const breadcrumb = [{title: 'Account'}, {link: '/accounts/customers', title: 'Customers'}, {title: 'Create Detail'}];


@Component({
  selector: 'ic-new-customer',
  templateUrl: './new-customer.component.html',
  styleUrls: ['./new-customer.component.scss']
})
export class NewCustomerComponent extends AbstractPageComponent implements OnInit {

  services: Array<string> = ['Botox', 'Filler', 'Da'];
  customer: Customer;
  customerProfile: CustomerProfile;

  @ViewChild('informationComponent', {static: false})
  informationComponent: CustomerInformationComponent;

  constructor(private customerApi: AccountService,
              private route: ActivatedRoute,
              private dialogService: DialogService) {
    super(title, breadcrumb);
  }

  ngOnInit() {
    this.customer = this.customerApi.createEmptyCustomer();
    this.customerProfile = new CustomerProfile({});
  }

  public updateProfileId() {

  }

  async canDeactivate(): Promise<boolean> {
    if (this.informationComponent.hasChanged()) {
      return await this.dialogService.openConfirm('Leave site?', 'Do you want to leave without save');
    } else {
      return true;
    }
  }
}
