import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Address, Contact, Customer, CustomerProfile, InitializerProvider, Phone} from '../../ic-core';
import {Observable} from 'rxjs';
import {UtilityService} from '../../ic-core';
import {CustomerRepository} from '../repository/customer.repository';
import {CustomerProfileRepository} from '../repository/customer-profile.repository';
import * as moment from 'moment';
import {Field} from '../../ic-core/decorators/decorators';

@Injectable({
  providedIn: 'root'
})
export class AccountService {
  constructor(protected customerRepository: CustomerRepository,
              protected customerProfileRepository: CustomerProfileRepository,
              protected utilityService: UtilityService) {
  }

  public async createCustomer(customer: Customer): Promise<Customer> {
    try {
      customer = await this.customerRepository.create(customer);
    } catch (err) {
      throw err;
    }
    return customer;
  }


  public async updateCustomer(dataChanged: Customer) {
    await this.customerRepository.update(dataChanged);
  }

  public async replaceCustomer(customer: Customer) {
    await this.customerRepository.replace(customer);
  }

  public async deleteCustomer(id) {
    await this.customerRepository.delete(id);
  }

  public searchCustomers(query: any): Observable<Customer[]> {
    return this.customerRepository.search({textSearch: query.searchString});
  }

  async getCustomer(customerId: string): Promise<Customer> {
    const customer: Customer = await this.customerRepository.get(customerId).toPromise();
    return customer;
  }

  createEmptyCustomer() {
    const name = '';
    // @ts-ignore
    const address: Address = {};
    address.city = '';
    address.district = '';
    address.street = '';

    // @ts-ignore
    const phone: Phone = {
      number: '',
      primary: true,
      type: Phone.TypeEnum.Mobile
    };
    // @ts-ignore
    const contact: Contact = {
      email: ``,
      addresses: [address],
      phones: [phone]
    };
    const customer: Customer = new Customer({
      fullName: name,
      birthday: null, // moment(new Date()).add(-13, 'year').toDate(),
      gender: null,
      photo: 'assets/images/xs/avatar.jpg',
      contact
    });
    return customer;
  }

// CustomerProfile

  async getCustomerProfile(customerId: string): Promise<CustomerProfile> {
    const customerProfiles: CustomerProfile[] =
      await this.customerProfileRepository.search({customerId}, `customers/${customerId}`).toPromise();
    let customerProfile: CustomerProfile;
    if (Array.isArray(customerProfiles) && customerProfiles.length > 0) {
      customerProfile = customerProfiles[0];
    } else {
      customerProfile = new CustomerProfile({customerId}); // init empty obj
    }
    this.initEmptyValueForCustomerProfile(customerProfile);
    return customerProfile;
  }

  initEmptyValueForCustomerProfile(customerProfile: CustomerProfile) {
    customerProfile.personalAnamnesis = customerProfile.personalAnamnesis || [];
    customerProfile.familyAnamnesis = customerProfile.familyAnamnesis || [];
    customerProfile.interests = customerProfile.interests || [];
    customerProfile.habits = customerProfile.habits || [];
    customerProfile.notes = customerProfile.notes || [];
    customerProfile.medicalHistories = customerProfile.medicalHistories || [];
    customerProfile.medicalHistories.push({});
    customerProfile.notes = customerProfile.notes || [];
    customerProfile.notes.push({});
    customerProfile.shared = customerProfile.shared || [];
  }

  async createOrUpdateCustomerProfile(customerId: string, customerProfileId: string, updateProperties: CustomerProfile) {
    updateProperties.customerId = customerId;
    if (Array.isArray(updateProperties.medicalHistories)) {
      updateProperties.medicalHistories = updateProperties.medicalHistories.filter(medicalHistory => {
        return Object.entries(medicalHistory).filter(([key, value]) => key !== 'id' && !['', null, undefined].includes(value)).length > 0;
      });
    }

    if (Array.isArray(updateProperties.notes)) {
      updateProperties.notes = updateProperties.notes.filter(note => {
        return Object.entries(note).filter(([key, value]) => key !== 'id' && !['', null, undefined].includes(value)).length > 0;
      });
    }

    if (!!customerProfileId) {
      updateProperties.id = customerProfileId;
      await this.customerProfileRepository.update(updateProperties, `customers/${customerId}`);
    } else {
      await this.customerProfileRepository.create(updateProperties, `customers/${customerId}`);
    }
  }

  // async replaceCustomerProfile(customerId: string, customerProfileId: string, customerProfile: CustomerProfile): Promise<CustomerProfile> {
  //   customerProfile.id = customerProfileId;
  //   customerProfile.customerId = customerId;
  //   await this.customerProfileRepository.replace(customerProfile, `customers/${customerId}`);
  //   this.initEmptyValueForCustomerProfile(customerProfile);
  //   return customerProfile;
  // }

}


