import { Injectable } from '@angular/core';
import {AbstractRepository, Customer, InitializerProvider} from '../../ic-core';
import {CustomerDao} from '../dao/customer.dao';
import {CustomerApi} from '../api/customer.api';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CustomerRepository extends AbstractRepository<Customer, CustomerDao, CustomerApi> {
  constructor(protected httpClient: HttpClient, private initializerProvider: InitializerProvider) {
    super(Customer, CustomerDao, CustomerApi, {httpClient, initializerProvider});
  }
}
