import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AccountService} from '../../services/account.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Customer, DialogService, CustomerProfile} from '../../../ic-core';
import {ErrorStateMatcher, MatDialog} from '@angular/material';
import {AbstractControl, FormControl, ValidatorFn, Validators} from '@angular/forms';
import * as jQuery from 'jquery';
import * as moment from 'moment';
import {$e} from 'codelyzer/angular/styles/chars';

function maxCustomerBirthdayValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    const date = control.value;
    if (!!date) {
      const age = moment().diff(date, 'years');
      if (age < 13) {
        return {maxCustomerBirthdayValidator: {value: control.value}};
      } else {
        return null;
      }
    } else {
      return null;
    }
  };
}

@Component({
  selector: 'ic-customer-information',
  templateUrl: './customer-information.component.html',
  styleUrls: ['./customer-information.component.scss']
})
export class CustomerInformationComponent implements OnInit, AfterViewInit {

  // tslint:disable-next-line:variable-name
  private _editMode: boolean;

  @Input('editMode')
  set editMode(mode: boolean) {
    this._editMode = !!mode;
    if (this._editMode) {
      this.emailFormControl.enable();
      this.phoneFormControl.enable();
      this.birthdayFormControl.enable();
    } else {
      this.emailFormControl.disable();
      this.phoneFormControl.disable();
      this.birthdayFormControl.disable();
    }

  }

  get editMode(): boolean {
    return this._editMode;
  }

  @Input() customer: Customer;
  backupCustomer: Customer;
  // @Output() onDataUpdate = new EventEmitter<void>();

  private updateProperties;

  public genders = [{value: true, title: 'Male'}, {value: false, title: 'Female'}];

  public phoneFormControl: FormControl = new FormControl({value: '', disabled: !this.editMode}, [
    Validators.required,
    Validators.pattern('^( *)([0-9]{10})( *)+$')
  ]);

  public birthdayFormControl: FormControl = new FormControl({value: '', disabled: !this.editMode}, [
    Validators.required
  ]);

  public emailFormControl: FormControl = new FormControl({value: '', disabled: !this.editMode}, [
    maxCustomerBirthdayValidator,
    Validators.email
  ]);

  public maxBirthDate = moment(new Date()).add(-13, 'year').toDate();

  public formControls = {
    phone: this.phoneFormControl,
    email: this.emailFormControl,
    birthday: this.birthdayFormControl
  };

  constructor(private accountService: AccountService,
              private route: ActivatedRoute,
              private router: Router,
              private dialogService: DialogService) {
  }


  ngOnInit() {
  }

  ngAfterViewInit(): void {

  }


  private async create(): Promise<boolean> {
    try {
      this.customer = await this.accountService.createCustomer(this.customer);
      return true;
    } catch (err) {
      const errorMessage = 'Create Failed';
      await this.dialogService.openAlert('', errorMessage);
      return false;
    }
  }

  private async update(): Promise<boolean> {
    this.updateProperties = {id: this.customer.id, ...this.updateProperties};
    try {
      await this.accountService.updateCustomer(this.updateProperties as Customer);
      return true;
    } catch (err) {
      const errorMessage = 'Update Failed';
      await this.dialogService.openAlert('', errorMessage);
      return false;
    }
  }

  public changeBirthday() {
    // setTimeout(() => {
    //   if (!!this.customer.birthday) {
    //     const age = moment().diff(this.customer.birthday, 'years');
    //     if (age < 13) {
    //       this.customer.birthday = null;
    //       this.dialogService.openAlert('Customer is too Young.');
    //     } else {
    //       this.changeProperty('birthday');
    //     }
    //   }
    // }, 250);


  }

  public changeProperty(propertyName: string) {
    this.updateProperties = this.updateProperties || {};
    this.updateProperties[propertyName] = this.customer[propertyName];
  }

  public availableSave(): boolean {
    return this.hasChanged() && this.isValidInput();
  }

  public hasChanged() {
    return !!this.updateProperties && Object.keys(this.updateProperties).length > 0;
  }

  public async toggleEdit() {
    if (this.editMode) {
      if (this.isValidInput()) {
        let result;
        if (!!this.customer.id) {
          result = await this.update();
        } else {
          result = await this.create();
          if (result) {
            this.updateProperties = undefined;
            await this.router.navigate([`accounts/customers/${this.customer.id}`]);
          }

        }
        this.editMode = !result;
        this.updateProperties = undefined;
      }
    } else {
      this.backupCustomer = new Customer(JSON.parse(JSON.stringify(this.customer)));
      this.editMode = true;
    }

  }

  public cancelEdit() {
    this.editMode = false;
    this.customer = this.backupCustomer;
    this.backupCustomer = undefined;
  }

  public isValidInput(): boolean {
    return !!this.customer.fullName &&
      !!this.customer.birthday &&
      moment(this.customer.birthday).isSameOrBefore(this.maxBirthDate) &&
      (this.customer.gender === true || this.customer.gender === false) &&
      !!this.customer.contact.phones[0].number &&
      // !!this.customer.contact.addresses[0].street &&
      // !!this.customer.contact.addresses[0].district &&
      // !!this.customer.contact.addresses[0].city &&
      !this.emailFormControl.invalid;
  }

  public getErrorMessage(inputField: string): string {
    let messageError = '';
    switch (inputField) {
      case 'email': {
        messageError = this.formControls.email.hasError('email') ? 'Not a valid email' : '';
        break;
      }
      case 'phone': {
        messageError = this.formControls.phone.hasError('required') ? 'Not a empty phone' :
          this.formControls.phone.hasError('pattern') ? 'Not a valid phone' :
            '';
        break;
      }
      case 'birthday': {
        messageError = !this.customer.birthday ? 'Not a empty birthday' :
          moment(this.customer.birthday).isBefore(this.maxBirthDate) ? 'Too Young' : '';
        break;
      }
    }

    return messageError;
  }

  changeAvatar(data: { file: File | Blob; dataUrl: string; base64String: string | ArrayBuffer }) {
    this.customer.photo = data.base64String as string;
    this.changeProperty('photo');
  }
}
