import {Injectable} from '@angular/core';
import {Customer, CustomerProfile, InitializerProvider} from '../../ic-core';
import {CustomerApi} from '../api/customer.api';
import {HttpClient} from '@angular/common/http';
import {CustomerProfileDao} from '../dao/customer-profile.dao';
import {CustomerProfileApi} from '../api/customer-profile.api';
import {AbstractDeepRepository} from '../../ic-core/repository/abstract.deep.repository';

@Injectable({
  providedIn: 'root'
})
export class CustomerProfileRepository extends AbstractDeepRepository<CustomerProfile, CustomerProfileDao, CustomerProfileApi> {
  constructor(protected httpClient: HttpClient, initializerProvider: InitializerProvider) {
    super(CustomerProfile, CustomerProfileDao, CustomerProfileApi, {httpClient, initializerProvider});
  }

  public async create(t: CustomerProfile, deepPath: string): Promise<CustomerProfile> {
    // skip create server side
    await this.dao.create(t);
    return t;
  }
}
