import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, CanDeactivate, Router, RouterStateSnapshot, UrlTree} from '@angular/router';;
import {AbstractPageComponent, PageLoaderService} from '../../ic-core';
import {AuthService} from '../../auth/services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class ConfirmChangedGuard implements CanDeactivate<AbstractPageComponent> {

  constructor(private router: Router, private authService: AuthService, private pageLoaderService: PageLoaderService) {
  }


  async canDeactivate(component: AbstractPageComponent,
                      currentRoute: ActivatedRouteSnapshot,
                      currentState: RouterStateSnapshot,
                      nextState?: RouterStateSnapshot): Promise<boolean> {
    setTimeout(() => {
      this.pageLoaderService.hidePageLoader();
    }, 500)
    return await  component.canDeactivate();
  }
}
